.projects {
	margin-top : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 32px;
	}
}

.projects__title {
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}

}

.projects__grid {
	display               : grid;
	grid-gap              : 12px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 24px;

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px 11px;
		grid-template-columns : repeat(9, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 24px 24px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 48px;
	}
}

.projects-item {
	background-color : $white;
	border           : 1px solid #CCD9E5;
	border-radius    : 8px;
	display          : flex;
	flex-direction   : column;
	grid-column      : 4 span;
	padding          : 16px 16px;

	@media screen and (min-width : $break-table) {
		grid-column : 3 span;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
		padding       : 24px 24px;
	}
}

.projects-item__img {
	border-radius : 8px;
	height        : 202px;
	margin-bottom : 16px;
	overflow      : hidden;

	@media screen and (min-width : $break-table) {
		height : 154px;
	}

	@media screen and (min-width : $break-desktop) {
		height        : 188px;
		margin-bottom : 16px;
	}

	img {
		display    : block;
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.projects-item__wrap {
	align-items   : center;
	display       : flex;
	gap           : 8px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

}

.projects-item__date {
	background-color : $light-grey;
	border-radius    : 24px;
	color            : $black;
	font-size        : 12px;
	font-weight      : 400;
	line-height      : 16px;
	padding          : 10px 10px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
	}
}

.projects-item__tag {
	background-color : $purple;
	border-radius    : 24px;
	color            : $white;
	font-size        : 12px;
	font-weight      : 400;
	line-height      : 16px;
	padding          : 10px 10px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
	}
}

.projects-item__name {
	color       : $black;
	font-size   : 14px;
	font-weight : 500;
	line-height : 18px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}
