.header-top {
	background-color : $light-grey;
	padding          : 12px 0;
}

.header-top__wrap {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.header-tel {
	color       : $black;
	font-size   : 14px;
	font-weight : 600;
	line-height : 20px;
}

.header-feedback {
	background-color : transparent;
	border           : none;
	color            : $green;
	font-size        : 14px;
	font-weight      : 400;
	line-height      : 20px;
	margin-left      : 20px;
}

.nav {
	display : flex;

	&__item {
		&:not(:first-child) {
			margin-left : 32px;
		}
	}

	&__link {
		color       : #828281;
		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;
	}
}

.header-bottom {
	padding-top : 16px;

	.b-btn {
		flex-shrink : 0;
	}
}

.header-bottom__wrap {
	align-items : center;
	display     : flex;
	width       : 100%;
}

.header-catalog {
	align-items : center;
	display     : flex;
	margin-left : 8px;
	width       : 100%;
}

.header-logo {
	flex-shrink  : 0;
	margin-right : 20px;
}

.header-search {
	width : 100%;
}

.header-search__form {
	margin-left : 12px;
	position    : relative;


	input {
		height : 49px;
		width  : 100%;
	}

	.b-btn--primary {
		padding  : 8px 10px;
		position : absolute;
		right    : 7px;
		top      : 5px;
		z-index  : 1;
	}
}

.header-cart {
	margin-left : 20px;
}

.cart-btn {
	background-color : transparent;
	border           : none;
	padding          : 0;

	&__icon {
		display        : inline-block;
		padding-bottom : 4px;
	}

	&__text {
		color       : $grey-2;
		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;
	}
}
