.requisites {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 92px;
	}
}

.requisites__title {
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.requisites__grid {
	display               : grid;
	grid-gap              : 12px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 32px 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 24px;
	}
}

.box {
	background-color : $light-grey;
	border-radius    : 8px;
	padding          : 16px 16px;

	@media screen and (min-width : $break-table) {
		padding : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
		padding       : 40px 40px;
	}
}

.box__head {
	align-items   : center;
	display       : flex;
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items    : flex-start;
		flex-direction : column;
		margin-bottom  : 32px;
	}
}

.box__icon {
	margin-right : 12px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
		margin-right  : 0;
	}
}


.box__name {
	color       : $black;
	font-size   : 14px;
	font-weight : 500;
	line-height : 18px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 28px;
	}
}

.box__list {
	li {
		border-bottom : 1px solid #CCD9E5;
		padding       : 16px 0;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			align-items : center;
			display     : flex;
			padding     : 24px 0;
		}

		&:first-child {
			border-top : 1px solid #CCD9E5;
		}
	}

	div {
		&:first-child {
			color         : #585857;
			font-size     : 12px;
			font-weight   : 400;
			line-height   : 16px;
			margin-bottom : 4px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				font-size     : 14px;
				line-height   : 20px;
				margin-bottom : 0;
				min-width     : 190px;
			}
		}

		&:last-child {
			color       : #2E2E2D;
			font-size   : 14px;
			font-weight : 500;
			line-height : 18px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				font-size   : 18px;
				line-height : 24px;
			}
		}
	}
}

.box__wrap {

}

.box--director {
	align-items : flex-start;
	display     : flex;
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		align-items : center;
	}

	.box__name {
		margin-bottom : 8px;
	}

	.box__wrap {
		margin-left : 12px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 24px;
		}
	}

	.box__icon {
		margin-bottom : 0;
		margin-right  : 0;
	}
}

.box__text {
	color       : #2E2E2D;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;
}

.box__table {
	//table-layout : fixed;

	tr {
		margin : 0;
	}

	tbody > tr > td {
		border-bottom : 1px solid #CCD9E5;
		padding       : 25px 0;

		&:first-child {
			border-top  : 1px solid #CCD9E5;
			color       : #585857;
			font-size   : 14px;
			font-weight : 400;
			line-height : 20px;
			min-width   : 195px;
		}

		&:last-child {
			border-top  : 1px solid #CCD9E5;
			color       : #2E2E2D;
			font-size   : 18px;
			font-weight : 500;
			line-height : 24px;
		}
	}
}

.box--info {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.box--requisites {
	grid-column : 4 span;


	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.box--contacts {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.tabs-buttons {
		margin-bottom : -1px;
	}
}

.map {
	grid-column : 4 span;


	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.icon--blue {
	background-color : $blue;
}
