.home-trust {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 98px;
	}
}

.home-trust__title {
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.home-trust__grid {
	display         : flex;
	gap             : 8px;
	justify-content : space-between;
	margin          : 0 -14px;
	overflow-x      : auto;

}

.home-trust__item {
	min-width : 140px;

	&:first-child {
		margin-left : 14px;
	}

	&:last-child {
		margin-right : 14px;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		min-width : 184px;
	}
}

.home-trust__img {
	background-color : $light-grey;
	border-radius    : 16px;
	height           : 78px;
	overflow         : hidden;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 102px;
	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.home-trust__name {
	color       : $grey-3;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	margin      : 16px auto 0 auto;
	max-width   : 150px;
	text-align  : center;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}
