// Arrow
.swiper-button-prev,
.swiper-button-next {
	cursor                      : pointer;
	display                     : inline-block;
	flex                        : 0 0 46px;
	height                      : 20px;
	outline                     : 0;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	transition                  : opacity .3s ease;
	user-select                 : none;
	vertical-align              : top;
	width                       : 46px;

	&:after {
		content  : '';
		left     : 0;
		position : absolute;
		top      : 0;
	}

	@media screen and (min-width : 1076px) {
		&:hover {
			opacity : .7;
		}
	}

	&.swiper-button-disabled {
		cursor         : auto;
		opacity        : 0.3;
		pointer-events : none;
	}
}

.swiper-button-prev {
	margin-right : 25px;

}

.swiper-button-next {
	margin-left : 25px;
}

.slider__pagination {
	display         : flex;
	flex-flow       : row nowrap;
	justify-content : space-between;
}

.swiper-button {
	cursor    : pointer;
	height    : 24px;
	position  : absolute;
	top       : 50%;
	transform : translateY(-50%);
	width     : 24px;
	z-index   : 2;
	@media screen and (min-width : $break-table) {
		height : 40px;
		width  : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--prev {
		left : 0;
	}

	&--next {
		right : 0;
	}
}
