.product {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
	}
}

.product__wrapper {
	display               : grid;
	grid-row-gap          : 32px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.product__slider {
	border-radius : 8px;
	grid-column   : 4 span;

	@media screen and (min-width : $break-table) {
		align-items : flex-start;
		display     : flex;
		gap         : 12px;
		grid-column : 1/7;
		grid-row    : 1/5;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
		grid-column   : 1/6;
		grid-row      : 1/3;
	}
}

.product__top {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		display         : flex;
		flex-direction  : column;
		grid-column     : 7/13;
		grid-row        : 1/5;
		height          : 100%;
		justify-content : space-between;
		margin-bottom   : 0;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		flex-direction  : row;
		grid-column     : 6/12;
		grid-row        : 1/1;
		justify-content : space-between;
		margin-bottom   : 30px;
	}
}

.product__info {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-right : 23px;
	}
}

.product__availability {
	align-items   : center;
	color         : #131313;
	display       : flex;
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 16px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 16px;
	}

	&--yes {
		&:before {
			background-image  : url("../images/icon/check-small.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			display           : inline-block;
			height            : 16px;
			margin-right      : 8px;
			width             : 16px;
		}
	}

	&--no {
		&:before {
			background-image  : url("../images/icon/close-small.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			display           : inline-block;
			height            : 16px;
			margin-right      : 8px;
			width             : 16px;
		}
	}
}

.product__name {
	color         : $black;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 32px;
		line-height   : 40px;
		margin-bottom : 16px;
	}
}

.product__article {
	color       : #ABABAB;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.product__wrap {
	background-color : #F7F9FB;
	border-radius    : 8px;
	padding          : 16px 16px;

	@media screen and (min-width : $break-table) {
		display         : flex;
		justify-content : space-between;
		padding         : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;
	}

	.b-btn--primary {
		min-width : 100%;
		@media screen and (min-width : $break-table) {
			min-width : 136px;
		}

		@media screen and (min-width : $break-desktop) {

		}

	}
}

.product__price {
	align-items   : center;
	display       : flex;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		align-items    : flex-start;
		flex-direction : column;
		margin-bottom  : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}

	.price-current {
		color       : #131313;
		font-size   : 24px;
		font-weight : 500;
		line-height : 28px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 2px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.price-old {
		color                : #828281;
		font-size            : 14px;
		font-weight          : 400;
		line-height          : 20px;
		margin-left          : 12px;
		text-decoration-line : line-through;

		@media screen and (min-width : $break-table) {
			margin-left : 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}


.product__about {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
		grid-row    : 5/5;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6/12;
		grid-row    : 2/3;
	}
}

.product__text {
	color       : #2E2E2D;
	font-size   : 14px;
	font-weight : 400;
	line-height : 22px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}


	p {
		&:not(:last-child) {
			margin-bottom : 12px;
		}
	}
}

.product-characteristics {

}

.product-characteristics__item {
	display : flex;

	@media screen and (min-width : $break-table) {
		align-items : center;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:not(:last-child) {
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}

	}
}

.product-characteristics__icon {
	margin-right : 12px;

	img {
		display : block;
	}
}

.product-characteristics__text {
	font-size   : 14px;
	font-weight : 500;
	line-height : 22px;
	max-width   : 180px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
		max-width   : none;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.product-documents {

	&__item {
		&:not(:last-child) {
			margin-bottom : 13px;
		}
	}

	&__link {
		align-items : center;
		display     : inline-flex;
	}

	&__icon {
		margin-right : 12px;

		img {
			display : block;
		}
	}

	&__name {
		color       : #2E2E2D;
		font-size   : 16px;
		font-weight : 500;
		line-height : 24px;
	}
}

.tabs-item {
	display : none;

	&.active {
		display : block;
	}
}

.tabs-buttons {
	border-bottom  : 1px solid #CCD9E5;
	margin-bottom  : 16px;
	padding-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom  : 24px;
		padding-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.tabs-btn {
	background-color : transparent;
	border           : none;
	color            : #ABABAB;
	font-size        : 14px;
	font-weight      : 400;
	line-height      : 22px;
	padding          : 0;
	position         : relative;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	& + & {
		margin-left : 12px;

		@media screen and (min-width : $break-table) {
			margin-left : 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&.active {
		color       : $blue;
		font-weight : 500;

		&:before {
			background-color : $blue;
			bottom           : -18px;
			content          : "";
			height           : 2px;
			left             : 0;
			position         : absolute;
			width            : 100%;

			@media screen and (min-width : $break-table) {
				bottom : -14px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.swiper--nav {
	display : none;

	@media screen and (min-width : $break-table) {
		display     : block;
		flex-shrink : 0;
		max-height  : 403px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.swiper-wrapper {

	}

	.swiper-slide {
		height : 52px;
		width  : 52px;
	}
}

.swiper--product {
	border-radius : 16px;

	.swiper-slide {
		img {
			border-radius : 16px;
			object-fit    : cover;
			overflow      : hidden;
			width         : 100%;
		}
	}

	.swiper-pagination {

		@media screen and (min-width : $break-table) {
			display : none;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
