.home-about {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
	}
}

.home-about__grid {
	display               : grid;
	grid-gap              : 24px 12px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(8, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.home-about__main {
	background-color : $light-grey;
	border-radius    : 8px;
	grid-column      : 4 span;
	height           : 100%;
	padding          : 16px 16px;

	@media screen and (min-width : $break-table) {
		display     : flex;
		gap         : 20px;
		grid-column : 8 span;
		padding     : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius  : 16px;
		flex-direction : column;
		gap            : 0;
		grid-column    : 6 span;
		grid-row       : 1/8;
		padding        : 64px 64px 40px 64px;
	}
}

.home-about__wraap {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		width         : 50%;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 52px;
		width         : 100%;
	}
}

.home-about__wrapper {
	display        : flex;
	flex-direction : column;


	@media screen and (min-width : $break-table) {
		height        : 100%;
		margin-bottom : 0;
		width         : 50%;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		width         : 100%;
	}

	&--page {
		@media screen and (min-width : $break-desktop) {
			justify-content : flex-end;
		}

	}
}

.home-about__title {
	margin-bottom : 6px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.home-about__text {
	color       : $grey-1;
	font-size   : 14px;
	font-weight : 400;
	line-height : 22px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.home-about__wrap {
	display : flex;
	gap     : 12px;

	@media screen and (min-width : $break-table) {
		gap             : 24px;
		justify-content : flex-end;
	}

	@media screen and (min-width : $break-desktop) {

		justify-content : space-between;
	}
}

.home-about__counts {
	background-color : $white;
	border-radius    : 8px;
	padding          : 16px 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
		padding       : 24px 24px;
	}

	div {
		&:first-child {
			color       : $blue;
			font-size   : 20px;
			font-weight : 500;
			line-height : 24px;

			@media screen and (min-width : $break-table) {
				font-size   : 24px;
				line-height : 28px;
			}

			@media screen and (min-width : $break-desktop) {
				font-size   : 40px;
				line-height : 48px;
			}
		}

		&:last-child {
			color       : $black;
			font-size   : 12px;
			font-weight : 400;
			line-height : 16px;
			margin-top  : 14px;

			@media screen and (min-width : $break-table) {
				margin-top : 24px;
			}

			@media screen and (min-width : $break-desktop) {
				font-size   : 16px;
				font-weight : 400;
				margin-top  : 26px;
			}
		}
	}
}

.home-about__link {
	display    : flex;
	margin-top : 24px;
	width      : 100%;

	@media screen and (min-width : $break-table) {
		justify-content : flex-end;
		margin-top      : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}

	a {
		align-items : center;
		color       : $grey-2;
		display     : flex;
		font-size   : 12px;
		font-weight : 400;
		line-height : 16px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}

		svg {
			flex-shrink : 0;
			height      : 24px;
			margin-left : 12px;
			width       : 24px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				height      : 40px;
				margin-left : 16px;
				width       : 40px;
			}
		}
	}
}

.home-about__slider {
	border-radius : 8px;
	grid-column   : 4 span;
	height        : 100%;
	//min-height    : 350px;

	@media screen and (min-width : $break-table) {
		grid-column : 8 span;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
		grid-column   : 6 span;
		grid-row      : 1/4;
		min-height    : 350px;
	}
}

.swiper--about {
	margin : 0 -14px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -16px;
	}


	.swiper-slide {
		picture {
			border-radius : 8px;
			display       : block;
			margin        : 0 14px;
			overflow      : hidden;


			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin : 0 16px;
			}
		}

		img {
			height     : 100%;
			object-fit : cover;
			width      : 100%;
		}
	}

	.swiper-button--next {
		right : 11px;

		@media screen and (min-width : $break-table) {
			right : 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.swiper-button--prev {
		left : -4px;

		@media screen and (min-width : $break-table) {
			left : 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

}

.home-about__item {
	border-radius : 8px;
	grid-column   : 2 span;
	padding       : 16px 16px;

	@media screen and (min-width : $break-table) {
		grid-column : 2 span;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 24px 24px;
	}

	&--yellow {
		background-color : $light-orange2;

		@media screen and (min-width : $break-table) {
			grid-column : 2 span;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 7/10;
			grid-row    : 4/6;
		}
	}

	&--orange {
		background-color : $linen;

		@media screen and (min-width : $break-table) {
			grid-column : 2 span;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 10/13;
			grid-row    : 4/6;
		}
	}

	&--purple {
		background-color : $light-purple;

		@media screen and (min-width : $break-table) {
			grid-column : 2 span;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 7/10;
			grid-row    : 6/8;
		}
	}

	&--green {
		background-color : $zanah;

		@media screen and (min-width : $break-table) {
			grid-column : 2 span;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 10/13;
			grid-row    : 6/8;
		}
	}

	.home-about__text {
		margin-bottom : 0;
		margin-top    : 16px;
		//max-width     : 170px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-top : 24px;
			max-width  : 170px;
		}
	}
}

.home-about__icon {
	height : 40px;
	width  : 40px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 50px;
		width  : 50px;
	}
}

.home-about__list {
	list-style   : disc;
	margin-top   : 8px;
	padding-left : 18px;

	li {
		color       : #2E2E2D;
		font-size   : 14px;
		font-weight : 400;
		line-height : 22px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}

		&:not(:last-child) {
			margin-bottom : 8px;
		}
	}

}
