.service {
	margin-top : 16px;

	@media screen and (min-width : $break-table) {
		margin-top : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.service__title {
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.service__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.service__img {
	border-radius : 8px;
	font-size     : 0;
	grid-column   : 1/5;
	overflow      : hidden;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
		grid-row    : 1/3;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}

.service__right {

}

.service__wrap {
	grid-column : 1/5;

	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 7/13;
	}
}


.service__about {
	color         : $black;
	font-size     : 24px;
	font-weight   : 500;
	line-height   : 28px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 20px;
		line-height   : 24px;
		margin-bottom : 14px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 24px;
	}
}

.service__list {
	li {
		align-items : center;
		color       : $grey-1;
		display     : flex;
		font-size   : 14px;
		font-weight : 400;
		line-height : 22px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}

		&:not(:last-child) {
			margin-bottom : 16px;
		}

		&:before {
			background-image : url("../images/icon/full-selection.svg");
			content          : "";
			display          : inline-block;
			height           : 18px;
			margin-right     : 12px;
			width            : 18px;
		}
	}
}

.service__price {
	align-items      : center;
	background-color : $light-grey;
	border-radius    : 8px;
	display          : flex;
	flex-wrap        : wrap;
	gap              : 16px;
	grid-column      : 1/5;
	margin-top       : auto;
	padding          : 16px 16px;

	@media screen and (min-width : $break-table) {
		align-items    : flex-start;
		flex-direction : column;
		gap            : 0;
		grid-column    : 7/13;
		grid-row       : 1/3;
		height         : 100%;
		padding        : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items    : center;
		border-radius  : 16px;
		flex-direction : row;
		grid-column    : 7/13;
		grid-row       : 2/2;
		height         : auto;
		padding        : 21px 24px;
	}

	.b-btn--primary {
		margin-top : auto;
		width      : 100%;

		@media screen and (min-width : $break-desktop) {
			margin-left : auto;
			width       : auto;
		}
	}
}

.service__group {

	&:not(:first-child) {

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 0;
			margin-left   : 48px;
		}
	}

	&:not(:last-child) {

		@media screen and (min-width : $break-table) {
			margin-bottom : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 0;
		}
	}

	div {
		&:first-child {
			color         : $grey-3;
			font-size     : 14px;
			font-weight   : 400;
			line-height   : 20px;
			margin-bottom : 2px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				font-size   : 16px;
				line-height : 24px;
			}
		}

		&:last-child {
			color       : $black;
			font-size   : 24px;
			font-weight : 500;
			line-height : 28px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}
