.home-services {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
	}
}

.home-services__title {
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.home-services__wrap {
	margin     : 0 -14px;
	overflow-x : auto;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin : 0;
	}
}

.home-services__grid {
	display               : grid;
	grid-gap              : 12px;
	grid-template-columns : repeat(12, 1fr);
	margin                : 0 -14px;
	overflow-x            : auto;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 24px;
	}
}

.services-item {
	background-repeat : no-repeat;
	background-size   : cover;
	border-radius     : 16px;
	display           : block;
	grid-column       : 3 span;
	height            : 100%;
	min-height        : 316px;
	min-width         : 200px;
	overflow          : hidden;
	padding           : 16px 16px;

	&:first-child {
		margin-left : 14px;
	}

	&:last-child {
		margin-right : 14px;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
		padding     : 32px 32px;
	}

	&--assembly {
		background-color : $solitaire;
		background-image : url("../images/home/services/services-1.jpg");


		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			grid-row   : 1/4;
			min-height : 545px;
		}
	}

	&--design {
		background-color : $linen;
		background-image : url("../images/home/services/services-2-mob.jpg");

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			background-image : url("../images/home/services/services-2.jpg");
			grid-row         : 1/2;
			min-height       : 260px;
		}
	}

	&--audit {
		background-color : $light-purple;
		background-image : url("../images/home/services/services-3-mob.jpg");

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			background-image : url("../images/home/services/services-3.jpg");
			grid-row         : 2/4;
			min-height       : 260px;
		}
	}

	&--froze {
		background-color : $zanah;
		background-image : url("../images/home/services/services-4.jpg");

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			grid-row   : 1/4;
			min-height : 545px;
		}
	}
}

.services-item__head {
	align-items     : flex-start;
	display         : flex;
	justify-content : space-between;
}

.services-item__wrap {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items : center;
		display     : flex;
	}
}

.services-item__name {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 18px;
	margin-top  : 12px;
	max-width   : 100px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
		margin-left : 16px;
		margin-top  : 0;
	}
}

.services-item__arrow {
	height : 24px;
	width  : 24px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 40px;
		width  : 40px;
	}
}


.icon {
	align-items     : center;
	border-radius   : 100%;
	display         : flex;
	flex-shrink     : 0;
	height          : 30px;
	justify-content : center;
	overflow        : hidden;
	width           : 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 64px;
		width  : 64px;
	}

	img {
		height : 15px;
		width  : 15px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			height : 24px;
			width  : 24px;
		}
	}

	&--yellow {
		background-color : $light-orange;
	}

	&--orange {
		background-color : $orange;
	}

	&--purple {
		background-color : $purple;
	}

	&--green {
		background-color : $green;
	}
}
