.interesting {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
	}
}

.interesting__title {
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.swiper--interesting {
	margin   : 0 -12px;
	position : relative;

	.swiper-slide {
		width : 50%;

		@media screen and (min-width : $break-table) {
			width : 25%;
		}

		@media screen and (min-width : $break-desktop) {

		}

		.goods-card {
			margin : 0 12px;
		}
	}

	.swiper-button {
		display : none;
		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.swiper-pagination {
		margin-top : 16px;
		@media screen and (min-width : $break-table) {
			display : none;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
