.catalog {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
	}
}

.catalog__title {
	margin-bottom : 20px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}


.catalog__gird {
	display               : grid;
	grid-gap              : 7px 12px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 19px 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 15px 24px;
	}

	.goods-card {
		grid-column : 2 span;

		@media screen and (min-width : $break-table) {
			grid-column : 3 span;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.catalog__gird--goods {
	grid-gap              : 24px 12px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px 24px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.catalog__item {
	align-items      : center;
	background-color : #F7F9FB;
	border-radius    : 9.95555px;
	display          : flex;
	flex-direction   : column;
	grid-column      : 2 span;
	padding          : 12px 5px 5px 5px;

	@media screen and (min-width : $break-table) {
		grid-column : 3 span;
		padding     : 12px 12px 8px 12px;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 20.48px;
		padding       : 30px 10px 10px 10px;
	}
}

.catalog__name {
	color         : $black;
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 16px;
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 20px;
	}
}

.catalog__img {
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : center;

	img {
		height     : 100px;
		object-fit : cover;
		width      : 100%;

		@media screen and (min-width : $break-table) {
			height : 125px;
		}

		@media screen and (min-width : $break-desktop) {
			height : auto;
		}

	}
}

.pagination {
	display         : flex;
	justify-content : center;
	margin-top      : 24px;
	width           : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 48px;
	}
}

.pagination__list {
	align-items : center;
	display     : flex;
}

.pagination__item {
	& + & {
		margin-left : 4px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 10px;
		}
	}

	&:first-child {

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-right : 16px;
		}
	}

	&:last-child {
		margin-left : 4px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 26px;
		}
	}
}

.pagination__link {
	align-items      : center;
	background-color : transparent;
	border-radius    : 6px;
	color            : $grey-2;
	display          : flex;
	font-size        : 12px;
	font-weight      : 400;
	height           : 30px;
	justify-content  : center;
	line-height      : 16px;
	transition       : background-color .3s ease, color .3s ease;
	width            : 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 8px;
		font-size     : 20px;
		height        : 40px;
		line-height   : 20px;
		width         : 40px;
	}

	&.active {
		background-color : $blue;
		color            : $white;
	}

	&:hover {
		background-color : $blue;
		color            : $white;

		svg {
			path {
				fill       : $white;
				transition : fill .3s ease;
			}
		}
	}

	svg {
		height : 30px;
		width  : 30px;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			height : 40px;
			width  : 40px;
		}

		path {
			color      : #585857;
			transition : fill .3s ease;
		}
	}
}
