.hero {
	margin-top : 32px;
}


.hero__item {
	margin   : 0 16px;
	position : relative;
}

.hero__image {
	img {

	}
}

.hero__wrap {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	left           : 0;
	padding-left   : 25px;
	padding-top    : 20px;
	position       : absolute;
	top            : 0;
	z-index        : 1;

	@media screen and (min-width : $break-table) {
		padding-left : 60px;
		padding-top  : 46px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-left : 100px;
		padding-top  : 80px;
	}
}

.hero__title {
	color       : $white;
	font-size   : 12px;
	font-weight : 500;
	line-height : 12px;
	margin-top  : 6px;
	max-width   : 95px;

	@media screen and (min-width : $break-table) {
		font-size   : 29.085px;
		line-height : 30px;
		margin-top  : 13px;
		max-width   : 233px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 50px;
		line-height : 52px;
		margin-top  : 24px;
		max-width   : 402px;
	}
}

.hero__text {
	color       : $white;
	font-size   : 6.20261px;
	font-weight : 400;
	line-height : 8px;
	margin-top  : 3px;
	max-width   : 74px;
	opacity     : 0.7;

	@media screen and (min-width : $break-table) {
		font-size   : 15.1242px;
		line-height : 20px;
		margin-top  : 7px;
		max-width   : 181px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 26px;
		line-height : 34px;
		margin-top  : 12px;
		max-width   : 312px;
	}
}

.nameplate {
	background-color : $green;
	border-radius    : 8px;
	color            : $white;
	font-size        : 3.81699px;
	font-weight      : 500;
	line-height      : 4px;
	padding          : 4px 4px;

	@media screen and (min-width : $break-table) {
		font-size   : 9.30719px;
		line-height : 9px;
		padding     : 9px 9px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 16px;
		padding     : 16px 16px;
	}

	&--small {
		font-size   : 8.01619px;
		line-height : 11px;
		padding     : 6px 6px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 12px;
			line-height : 16px;
			padding     : 8px 8px;
		}
	}
}

.swiper--hero {
	margin : 0 -16px;

	.swiper-slide {
		img {
			border-radius : 16px;
			overflow      : hidden;
		}
	}

	.swiper-button {
		display : none;

		@media screen and (min-width : $break-table) {
			display : block;
		}
	}
}
