.error {
	padding : 72px 0;
}

.error__wrapper {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	text-align     : center;
}

.error__img {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 10px;
	}
}

.error__title {
	color         : $black;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 6px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 32px;
		line-height   : 40px;
		margin-bottom : 12px;
	}
}

.error__text {
	color       : $grey-1;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	margin      : 0 auto;
	max-width   : 260px;
	opacity     : 0.7;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 18px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
		max-width   : 260px;
	}
}

.error__wrap {
	margin-bottom : 24px;
}
