.home-category {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 48px;
	}
}

.category-item {
	background-color : #DDDBEC;
	border-radius    : 16px;
	display          : block;
	position         : relative;

	&__name {
		color       : #131313;
		display     : block;
		font-size   : 12px;
		font-weight : 400;
		left        : 50%;
		line-height : 16px;
		padding     : 12px 10px 0 10px;
		position    : absolute;
		text-align  : center;
		top         : 0;
		transform   : translateX(-50%);
		width       : 120px;
		z-index     : 2;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 14px;
			line-height : 20px;
			padding     : 24px 19px 0 19px;
			width       : 187px;
		}
	}

	&__image {
		img {
			object-fit : cover;
			width      : 100%;
		}
	}
}

.swiper--category {
	margin : 0 -6px;

	@media screen and (min-width : $break-table) {
		margin : 0 -1.5px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -10px;
	}

	.swiper-slide {
		width : 50%;

		@media screen and (min-width : $break-table) {
			width : 20%;
		}

		@media screen and (min-width : $break-desktop) {

		}

		.category-item {
			margin : 0 6px;

			@media screen and (min-width : $break-table) {
				margin : 0 1.5px;
			}

			@media screen and (min-width : $break-desktop) {
				margin : 0 10px;
			}
		}
	}


}

