.project {
	margin-top : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 32px;
	}
}

.project__wrapper {
	margin    : 0 auto;
	max-width : 1020px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.project__main {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		align-items   : flex-start;
		display       : flex;
		gap           : 24px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 96px;
	}

	.project__img {
		margin-bottom : 12px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}
	}
}

.project__date {
	color         : $grey-3;
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 16px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 32px;
	}
}

.project__name {
	color         : $black;
	font-size     : 20px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 6px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 12px;
	}
}

.project__about {
	color       : $grey-1;
	font-size   : 14px;
	font-weight : 400;
	line-height : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.project__group {
	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 96px;
		}
	}

	.project__img {
		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 32px;
			}
		}
	}
}

.project__text {
	color       : $grey-1;
	font-size   : 14px;
	font-weight : 400;
	line-height : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 32px;
			}
		}
	}
}

.project__img {
	border-radius : 8px;
	overflow      : hidden;

	@media screen and (min-width : $break-table) {
		max-width : 600px;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}
