.home-goods {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
	}
}

.home-goods__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 25px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.home-goods__wrap {
	align-items : center;
	display     : flex;

	.nameplate--small {
		margin-left : 16px;
	}
}

.home-goods__grid {
	display               : grid;
	grid-column-gap       : 12px;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(6, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.goods-card {
	background-color : $white;
	border           : 1px solid transparent;
	border-radius    : 16px;
	grid-column      : 3 span;
	position         : relative;
	transition       : border .3s ease, box-shadow .3s ease;

	@media screen and (min-width : $break-table) {
		padding : 12px 12px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:hover {
		border     : 1px solid #F7F9FB;
		box-shadow : 1px 1px 10px 2px rgba(208, 208, 208, 0.2);
	}
}

.good-card__img {
	align-items      : center;
	background-color : $light-grey;
	border-radius    : 16px;
	display          : flex;
	flex-direction   : column;
	height           : 140px;
	justify-content  : center;
	overflow         : hidden;
	position         : relative;

	@media screen and (min-width : $break-table) {
		height : 160px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 288px;
	}

	img {
		height     : 120px;
		object-fit : cover;
		width      : 100%;

		@media screen and (min-width : $break-table) {
			height : 150px;
		}

		@media screen and (min-width : $break-desktop) {
			height : auto;
		}
	}
}

.goods-card__price {
	margin-top : 10px;

	@media screen and (min-width : $break-table) {
		margin-top : 14px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 12px;
	}

	.price {
		color       : $black;
		font-size   : 14px;
		font-weight : 500;
		line-height : 18px;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 18px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 20px;
			line-height : 28px;
		}
	}

	.price-old {
		color                : $grey-3;
		font-size            : 12px;
		font-weight          : 400;
		line-height          : 16px;
		padding-left         : 8px;
		text-align           : center;
		text-decoration-line : line-through;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size    : 14px;
			line-height  : 20px;
			padding-left : 16px;
		}
	}
}

.goods-card__name {
	color       : $black;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	margin-top  : 6px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
		margin-top  : 8px;
	}
}

.goods-card__availability {
	align-items : center;
	color       : $black;
	display     : flex;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	margin-top  : 6px;

	@media screen and (min-width : $break-table) {
		margin-top : 8px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
	}

	&--yes {
		&:before {
			background-image  : url("../images/icon/check-small.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			display           : inline-block;
			height            : 16px;
			margin-right      : 8px;
			width             : 16px;
		}
	}

	&--no {
		&:before {
			background-image  : url("../images/icon/close-small.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			display           : inline-block;
			height            : 16px;
			margin-right      : 8px;
			width             : 16px;
		}
	}
}

.goods-card__btn {
	margin-top : 30px;
	padding    : 6px 18px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 14px 16px;
	}

	svg {
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	span {
		display : none;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}
	}
}

.promotion {
	background-color : #554DA0;
	border-radius    : 8px;
	color            : $white;
	font-size        : 8px;
	font-weight      : 500;
	left             : 6px;
	line-height      : 11px;
	padding          : 6px 6px;
	position         : absolute;
	top              : 6px;
	z-index          : 1;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size : 12px;
		left      : 8px;
		padding   : 8px 8px;
		top       : 8px;
	}
}
