.breadcrumbs {
	padding-top : 18px;

	@media screen and (min-width : $break-table) {
		padding-top : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-top : 48px;
	}
}

.breadcrumbs__list {
	align-items : center;
	display     : flex;
	flex-wrap   : wrap;
}

.breadcrumbs__item {
	position : relative;

	&:not(:first-child) {
		margin-left : 34px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 38px;
		}

		&:before {
			background-image  : url("../images/icon/arrow.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			height            : 5px;
			left              : -23px;
			position          : absolute;
			top               : 55%;
			transform         : translateY(-50%);
			width             : 10px;
			z-index           : 1;

			@media screen and (min-width : $break-table) {
				height : 10px;
				left   : -29px;
				top    : 50%;
				width  : 17px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.breadcrumbs__link {
	color       : $grey-1;
	font-size   : 12px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}

	img {
		display : block;
		height  : 16px;
		width   : 16px;

		@media screen and (min-width : $break-table) {
			height : 20px;
			width  : 20px;
		}
	}
}
