* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : inherit;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : hidden;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}

.main--offset {
	border        : 1px solid #CCD9E5;
	border-radius : 8px;
}

.container {
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	margin             : 0 auto;
	max-width          : 1256px;
	padding            : 0 14px;
	width              : 100%;

	@media screen and (min-width : $break-table) {
		padding : 0 16px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.section-title {
	color       : $black;
	font-size   : 20px;
	font-weight : 500;
	line-height : 22px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 32px;
		line-height : 40px;
	}
}

.link-head {
	color       : $blue;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}
