.home-news {
	background-color : $light-grey;
	margin-top       : 32px;
	padding          : 32px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
		padding    : 96px 0;
	}
}

.home-news__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.home-news__grid {
	display               : grid;
	grid-gap              : 12px;
	grid-template-columns : repeat(12, 1fr);
	margin                : 0 -14px;
	overflow-x            : auto;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 24px;
	}
}

.news-item {
	align-items      : flex-start;
	background-color : $white;
	border           : 1px solid #CCD9E5;
	border-radius    : 8px;
	display          : flex;
	flex-direction   : column;
	grid-column      : 3 span;
	min-width        : 230px;
	padding          : 16px 16px 48px 16px;

	&:first-child {
		margin-left : 14px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&:last-child {
		margin-right : 14px;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
		padding       : 24px 24px 86px 24px;
	}
}


.news-item__img {
	background-color : $light-grey;
	border-radius    : 8px;
	height           : 154px;
	width            : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 16px;
		height        : 188px;
	}
}

.news-item__main {
	margin-top : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.news-item__date {
	background-color : $light-grey;
	border-radius    : 24px;
	color            : $black;
	font-size        : 14px;
	font-weight      : 400;
	line-height      : 20px;
	padding          : 10px 10px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.news-item__link {
	color       : $black;
	display     : inline-block;
	font-size   : 14px;
	font-weight : 500;
	line-height : 18px;
	margin-top  : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
		margin-top  : 26px;
	}
}
