.checkbox-style > span:after {
  background: #000;
  border-radius: 3px;
  content: "";
  height: 14px;
  left: 3px;
  opacity: 0;
  position: absolute;
  top: 3px;
  transition: opacity 0.3s ease;
  width: 14px;
  z-index: 5;
}
