.certificates {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
	}

}

.certificates__title {
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.certificates__wrap {
	display               : grid;
	grid-row-gap          : 12px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.certificates-item {
	align-items   : flex-start;
	border        : 1px solid #CCD9E5;
	border-radius : 16px;
	display       : flex;
	grid-column   : 4 span;
	padding       : 16px 16px;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		align-items : center;
		padding     : 29px 24px;
	}
}

.certificates-item__name {
	color       : #131313;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
	}
}

.certificates-item__size {
	color       : #2E2E2D;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.certificates-item__wrap {
	margin-left : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 24px;
	}
}
