.feedback {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {
	
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 92px;
	}
}

.feedback__title {
	margin-bottom : 6px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 16px;
	}
}

.feedback__text {
	color       : $grey-1;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	max-width   : 206px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
		max-width   : 241px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
		max-width   : 284px;
	}
}

.feedback__top {
	margin-bottom : 20px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
		margin-bottom   : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;
		margin-bottom  : 40px;
	}
}

.feedback__wrap {
	background-color : $light-grey;
	margin           : 0 -14px;

	@media screen and (min-width : $break-table) {
		border-radius : 8px;
		margin        : 0;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius   : 16px;
		display         : flex;
		justify-content : space-between;
	}
}


.feedback__left {
	margin-bottom : 32px;
	padding       : 32px 14px 0 14px;

	@media screen and (min-width : $break-table) {
		border-radius : 8px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		padding       : 64px 0 64px 64px;
	}
}

.feedback__right {
	align-items     : center;
	display         : flex;
	flex-direction  : column;
	justify-content : flex-end;
	min-height      : 100%;
	padding         : 0 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 64px;
	}
}
