.footer {
	margin-top : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 96px;
	}
}

.footer__top {
	padding : 0 0 16px 0;

	@media screen and (min-width : $break-table) {
		border-bottom : 1px solid #CCD9E5;
		border-top    : 1px solid #CCD9E5;
		padding       : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer__menu {

	@media screen and (min-width : $break-table) {
		display         : flex;
		flex-wrap       : wrap;
		gap             : 36px;
		justify-content : start;
	}

	@media screen and (min-width : $break-desktop) {
		flex-wrap       : nowrap;
		gap             : 20px;
		justify-content : space-between;
	}
}

.footer__item {
	border-bottom : 1px solid #CCD9E5;
	padding       : 17px 0;
	width         : 100%;

	&:first-child {
		border-top : 1px solid #CCD9E5;
		@media screen and (min-width : $break-table) {
			border-top : none;
		}
	}

	@media screen and (min-width : $break-table) {
		border  : none;
		padding : 0;
		width   : 30%;
	}

	@media screen and (min-width : $break-desktop) {
		width : 20%;
	}

	&.open {
		.menu-item {
			margin-top : 16px;
			opacity    : 1;
			overflow   : visible;
			visibility : visible;
		}

		.footer__head {
			img {
				transform  : rotate(180deg);
				transition : transform .3s ease;
			}
		}
	}
}

.footer__head {
	align-items      : center;
	background-color : transparent;
	border           : none;
	color            : $black;
	cursor           : unset;
	display          : flex;
	font-size        : 16px;
	font-weight      : 500;
	justify-content  : space-between;
	line-height      : 24px;
	padding          : 0;
	text-align       : left;
	width            : 100%;

	img {
		transform  : rotate(0);
		transition : transform .3s ease;

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.menu-item {
	box-sizing  : content-box;
	max-height  : 0;
	opacity     : 0;
	overflow    : hidden;
	transition  : .3s ease-out;
	visibility  : hidden;
	will-change : max-height;

	@media screen and (min-width : $break-table) {
		max-height : none;
		opacity    : 1;
		visibility : visible;

	}

	@media screen and (min-width : $break-desktop) {

	}

	li {
		&:not(:last-child) {
			margin-bottom : 14px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 12px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 16px;
			}
		}

		a {
			color       : $grey-2;
			font-size   : 14px;
			font-weight : 400;
			line-height : 20px;
		}
	}
}

.footer__middle {
	border-bottom  : 1px solid #CCD9E5;
	padding-bottom : 16px;

	@media screen and (min-width : $break-table) {
		align-items     : end;
		display         : flex;
		justify-content : space-between;
		padding         : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer__contacts {
	span {
		color         : #585857;
		display       : block;
		font-size     : 12px;
		font-weight   : 400;
		line-height   : 16px;
		margin-bottom : 8px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 12px;
		}

		@media screen and (min-width : $break-desktop) {

		}

	}
}

.footer__wrap {
	display               : grid;
	grid-template-columns : repeat(2, 1fr);
	margin-bottom         : 24px;
	position              : relative;

	@media screen and (min-width : $break-table) {
		display       : flex;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	a {
		color       : $blue;
		font-size   : 14px;
		font-weight : 600;
		line-height : 18px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&:last-child {

			@media screen and (min-width : $break-table) {
				margin-left : 24px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-left : 50px;
			}
		}
	}

	&:before {
		background-color : #CCD9E5;
		content          : "";
		height           : 24px;
		left             : 52%;
		opacity          : 0;
		position         : absolute;
		top              : 0;
		transform        : translateX(-50%);
		width            : 1px;

		@media screen and (min-width : $break-table) {
			opacity : 1;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.footer__nav {
	display               : grid;
	grid-row-gap          : 16px;
	grid-template-columns : repeat(2, 1fr);

	@media screen and (min-width : $break-table) {
		align-items : center;
		display     : flex;
	}

	@media screen and (min-width : $break-desktop) {

	}

	li {
		grid-column : 1 span;

		&:not(:first-child) {
			@media screen and (min-width : $break-table) {
				margin-left : 24px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-left : 32px;
			}
		}

		a {
			color       : #585857;
			font-size   : 14px;
			font-weight : 400;
			line-height : 20px;
		}
	}
}

.footer__bottom {
	display               : grid;
	grid-template-columns : repeat(2, 1fr);
	padding               : 24px 0 14px 0;

	@media screen and (min-width : $break-table) {
		display         : flex;
		justify-content : space-between;
		padding         : 24px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	a {
		color       : #585857;
		font-size   : 10px;
		font-weight : 400;
		line-height : 14px;

		@media screen and (min-width : $break-table) {
			font-size   : 12px;
			line-height : 16px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
